.navbar_body {
  width: 100%;
  height: 62px;
  padding: 0px 75px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.navbar_body .logo {
  height: 40%;
  object-fit: contain;
}

.navbar_body .divider_pipe {
  font-size: 25px;
  margin-left: 30px;
  margin-bottom: 8px;
  margin-right: 30px;
  font-weight: 300;
}

.navbar_body .title {
  font-size: 19px;
  font-weight: 400;
}

.navbar_body .profile {
  height: 35px;
  width: 50px;
  /* border-radius: 100%;
  border: 0.5px solid rgb(133, 133, 133); */
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
/* 
.profile .icon {
  height: 50%;
} */

.button{
  background-color: #6456d1;
  width: 10rem;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  margin: 7px 100px 4px 2px;
  cursor: pointer;
  border-radius: 5px;
}
